<template>
  <div style="background:rgb(245, 245, 245)">
    <van-nav-bar
    :fixed="true"
      :title="normal.value1"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="selectDataPicStation"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <van-icon name="filter-o" slot="right" size="20" />
    </van-nav-bar>
    <div class="legelList border-radius" v-show="show===0">
      <mu-list
        toggle-nested
        v-for="(item,index) in list"
        :key="index"
        style="padding:0"
        @click="clickBtnToWindow(item.path)"
      >
        <mu-list-item
          button
          :ripple="false"
          nested
          :open="open === 'send'"
          @toggle-nested="open = arguments[0] ? 'send' : ''"
          class="border-bottom"
        >
          <mu-list-item-title :style="'color:'+$store.state.color">{{item.name}}</mu-list-item-title>
          <mu-list-item-action>
            <mu-icon class="toggle-icon" color="#ccc" size="30" value="keyboard_arrow_right"></mu-icon>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
    </div>
    <div v-show="show===1" class="legelList border-radius">
      <mu-list
        toggle-nested
        v-for="(item,index) in listContent"
        :key="index"
        style="padding:0"
        @click="clickBtnTo(item.station)"
      >
        <mu-list-item
          button
          :ripple="false"
          nested
          :open="open === 'send'"
          @toggle-nested="open = arguments[0] ? 'send' : ''"
          class="border-bottom"
        >
          <mu-list-item-title :style="'color:'+$store.state.color">{{item.station}}</mu-list-item-title>
          <mu-list-item-action>
            <mu-icon class="toggle-icon" color="#ccc" size="30" value="keyboard_arrow_right"></mu-icon>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
    </div>
    <div v-show="show===2" class="legelList border-radius">
      <div class="Filetitle">
        <div class="cont" style="flex:1">项目</div>
        <div class="cont" style="flex:2">标准排放浓度</div>
        <div class="cont right" style="flex:4">依据</div>
      </div>
      <div class="bottom">
        <div
          class="fileContent Filetitle"
          v-for="(item,index) in $store.state.standardTable"
          :key="index"
        >
          <div class="cont1" style="flex:1">{{item.item}}</div>
          <div class="cont1" style="flex:2">{{item.standard}}</div>
          <div class="cont1 right" style="flex:4">{{item.basis}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
let url = "http:47.112.16.168:";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  name: "maintenanceChild3",
  data() {
    return {
      stationMenu: [],
      opened: false,
      filterable: {
        value1: ""
      },
      citys: [
        "污水综合排放标准",
        "水污染源在线检测系统安装技术规范",
        "污染源在线自动监控(监测)系统数据传输标准",
        "污染源自动监控设备管理法"
      ],
      open: "send",
      options: ["法律法规", "技术文档", "排放标准", "报告报表"],
      options1: ["法律法规", "技术文档", "日报周报月报"],
      value3: null,
      normal: {
        value1: "法律法规",
        value2: null
      },
      list: [
        { content: "污水综合排放标准" },
        { content: "水污染源在线检测系统安装技术规范" },
        { content: "污染源在线自动监控(监测)系统数据传输标准" },
        { content: "污染源自动监控设备管理法" },
        { content: "多参数自动分析仪操作规范" }
      ],
      checkAll: true,
      token: "",
      show: 0,
      listContent: [],
      loading: false,
      listContentReport: [],
      value1: ""
    };
  },
  methods: {
    Test() {
      Toast("正处调试阶段，请稍后！");
    },
    selectDataPicStation() {
      this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.options, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.opened = false;
          switch (selectArr[0]) {
            case "法律法规":
              _this.show = 0;
              break;

            case "技术文档":
              _this.show = 1;
              break;
            case "排放标准":
              _this.show = 2;
              break;
          }
        }
      });
    },
    confirmQuery() {
      this.opened = false;
    },
    openBotttomSheet() {
      this.opened = true;
    },
    closeBottomSheet() {
      this.opened = false;
    },
    clickBtnToWindow(e) {
      window.open(url + e);
    },
    clickBtnTo(e) {
      this.$router.push({
        path: "/FilesDetails",
        query: { name: e, id: "Event" }
      });
    },
    // 法律文档
    async initDataList() {
      var url = "/system/file/statute";
      var pageNum = 1;
      var pageSize = 100000000;
      var search = this.value1;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          search
        },
        this.token
      );
      this.list = res.data.data.rows;
      console.log(res);
    },
    // 技术文档
    async initDataList1() {
      var url = "/system/file/technical";
      var pageNum = 1;
      var pageSize = 100000000;
      var search = this.value1;
      var res = await AppBace.request(
        url,
        "GET",
        {
          search
        },
        this.token
      );
      console.log("res", res);
      var arr = res.data.data;
      var array = [];
      for (let index = 0; index < arr.length; index++) {
        var station = arr[index].station;
        array.push(station);
      }
      if (array.length > 1) {
        this.citys = array;
      }
      this.listContent = res.data.data;
    },
    fullscreen() {
      this.loading = this.$loading();
    }
  },
  async mounted() {
    this.token = sessionStorage.getItem("token");
    await this.fullscreen();
    await this.initDataList1();
    await this.initDataList();
    this.loading.close();
  }
};
</script>
<style scoped>
@import "../../../static/css/selectCommon.css";
@import "../../../static/css/model.css";
.cont {
  display: flex;
  justify-content: center;
  height: 40px;
  align-items: center;
  border-left: 1px solid #ccc;
  border-top: #ccc 1px solid;
  border-bottom: 1px solid #ccc;
  font-weight: 700;
}
.cont1 {
  display: flex;
  justify-content: center;
  height: 40px;
  align-items: center;
  border-left: 1px solid #ccc;
  border-top: #ccc 1px solid;
  text-overflow: ellipsis;
  word-break: keep-all; /* 不换行 */
  overflow: hidden; /* 内容超出宽度时隐藏超出部分的内容 */
  font-size: 12px;
  /* border-bottom: 1px solid #ccc; */
}
.right {
  border-right: #ccc solid 1px;
}
.Filetitle {
  display: flex;
  flex-direction: row;
}
.bottom {
  border-bottom: 1px solid #ccc;
}
.contanerMain {
  margin: 0 5px;
  background: white;
  padding: 10px;
  border-radius: 10px;
  margin-top: 10px;
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 0 5px;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  /* border-bottom: 1px #ccc solid; */
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 40px;
  display: flex;
  /* align-items: center; */
  align-content: center;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: blue;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
.legelList {
  background: white;
  margin: 10px;
  /* border-radius: 10px; */
  margin-top: 56px;
}
.legelList1 {
  position: fixed;
  top: 55px;
  width: 100%;
  background: #eefaeb;
  /* border-bottom: 3px solid rgb(165, 165, 165); */
  box-shadow: 0px 2px 0px #ccc;
  height: 60px;
  z-index: 9999;
  display: flex;
  align-items: center;
  /* padding-top:10px;  */

  /* margin-top: 65px; */
}
.absolute {
  margin: 0 5px;
  color: red;
}
.absolute img {
  width: 20px;
  height: 20px;
}
.flexHistory {
  display: flex;
  flex-direction: row;
}
#input {
  border: none;
  width: 84%;
  outline: none;
  height: 32px;
  /* padding: 0 5px; */
  text-align: center;
}
.border {
  border-bottom: 1px solid #ccc;
}
.none {
  border: none;
}
.border-bottom {
  border-bottom: rgb(235, 234, 234) 1px solid;
}
.leftImage img {
  width: 25px;
  height: 25px;
}
.headerCon {
  position: fixed;
  top: 0;
  width: 100%;
}
.imageSearch img {
  width: 30px;
  height: 30px;
}
.imageSearch {
  display: flex;
  align-items: center;
}
#input {
  border: none;
  outline: none;
  width: 100%;
}
.inputFlex {
  flex: 1;
}
.flexSearch {
  display: flex;
  flex-direction: row;
  background: white;
  margin: 0 10px;
  border-radius: 10px;
  padding: 0 10px;
  width: 95%;
}
</style>



